@import "fonts";
@import "vars";
@import "global";
@import "mixins";

@import "parts/modal";

.header {
	z-index: 1;
	position: absolute;
	width: 100%;
	top: 86px;

	@media (max-width: ($md2 + px)) {
		top: 105px;
	}

	@media (max-width: ($md3 + px)) {
		top: 80px;
	}
	@media (max-width: ($md4 + px)) {
		background-color: #fff8f8;
		border-radius: 0 0 64% 64%;
		top: -10px;
	}
	// .header__container

	&__container {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@media (max-width: ($md3 + px)) {
			align-items: center;
		}
		@media (max-width: ($md4 + px)) {
			flex-direction: column;
			gap: 15px;
		}
	}

	&__leftside {
		display: flex;
		gap: 21px;
		position: relative;
		z-index: 1;
		align-items: center;
		@media (max-width: ($md3 + px)) {
			gap: 10px;
		}

		@media (max-width: ($md4 + px)) {
			padding-top: 80px;
		}
	}
	&__blob {
		position: absolute;
		z-index: -1;
		left: -198px;
		top: -455px;

		@media (max-width: ($md2 + px)) {
			left: -330px;
			top: -455px;
		}

		@media (max-width: ($md3 + px)) {
			left: -350px;
			top: -485px;
		}
		@media (max-width: ($md4 + px)) {
			//top: -280%;
			//left: 0;
			//width: 590px;
			display: none;
		}
	}

	&__logo {
		@media (max-width: ($md3 + px)) {
			max-width: 50px;
		}
	}
	&__logo-text {
		font-family: "SignPainter-HouseScript";
		line-height: 1;
		margin-top: 4px;
		color: #000000;

		@media (max-width: ($md3 + px)) {
			font-size: 25px;
		}
	}
	&__button {
		width: 350px;
		height: 70px;
		box-shadow: 0 15px 40px rgba(92, 194, 62, 0.2), inset 0 0 20px rgba(255, 255, 255, 0.2);
		border-radius: 12px;
		background-color: #61cb41;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #ffffff;
		font-size: 18px;
		font-weight: 500;
		line-height: calc(28 / 18 * 100%);
		text-align: center;
		gap: 14px;
		&:hover {
			box-shadow: 0 15px 40px rgba(92, 194, 62, 0.2), inset 0 0 20px rgba(255, 255, 255, 0.2);
			background-color: #46d61a;
		}
		&:active {
			box-shadow: none;
			background-color: #61cb41;
		}
		@media (max-width: ($md3 + px)) {
			font-size: 16px;
			padding: 0 15px;
			width: 100%;
			svg {
				display: none;
				@media (max-width: ($md4 + px)) {
					display: block;
				}
			}
		}
	}
}

.main-nav {
	width: 100%;
	position: fixed;
	top: 0;
	z-index: 11;
	box-shadow: 0 25px 60px rgba(163, 169, 183, 0.1);
	background-color: #fdfdfd;
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 15px 0;
		gap: 55px;

		@media (max-width: ($md1 + px)) {
			gap: 30px;
		}
		@media (max-width: ($md3 + px)) {
			padding: 10px 15px;
		}
	}
	// .main-nav__list
	&__toggler {
		display: none;
		max-width: 30px;
		width: 100%;
		@media (max-width: ($md3 + px)) {
			display: block;
		}
	}
	&__list {
		display: flex;
		justify-content: space-between;
		width: 100%;
		max-width: 1000px;
		margin: auto;

		@media (max-width: ($md2 + px)) {
			padding-left: 15px;
			display: grid;
			grid-template-columns: repeat(4, auto);
		}

		@media (max-width: ($md3 + px)) {
			display: none;

			&#nav-mobile {
				display: grid;
				grid-template-columns: auto;
				padding-left: 30px;
				gap: 20px;
			}
		}
	}

	// .main-nav__list-item

	&__list-item {
		position: relative;
		&::before {
			position: absolute;
			content: "";
			width: 6px;
			height: 6px;
			background-color: #61cb41;
			border-radius: 50%;
			left: -42px;
			top: 9px;

			@media (max-width: ($md1 + px)) {
				left: -20px;
			}
			@media (max-width: ($md2 + px)) {
				left: -20px;
			}

			@media (max-width: ($md3 + px)) {
				top: 13px;
			}
		}
		&:first-of-type::before {
			opacity: 0;
			@media (max-width: ($md2 + px)) {
				opacity: 1;
			}
		}
	}

	// .main-nav__list-link

	&__list-link {
		font-size: 16px;
		font-weight: 500;
		line-height: calc(26 / 16 * 100%);

		@media (max-width: ($md3 + px)) {
			font-size: 20px;
			font-weight: 400;
		}
	}

	&__offcanvas {
		max-width: 80%;
	}
	&__offcanvas-header {
		justify-content: flex-end;
	}
}

.section-1 {
	color: #fff;
	background-image: url(../img/sec1-back.webp);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: top center;
	border-radius: 0 0 140px 140px;
	padding-top: 236px;

	position: relative;
	overflow: hidden;
	@media (max-width: ($md3 + px)) {
		padding-top: 210px;
		border-radius: 0 0 50px 50px;
	}

	@media (max-width: ($md4 + px)) {
		padding-top: 250px;
	}
	// .section-1__container

	&__container {
	}

	// .section-1__logo

	&__logo {
		z-index: 2;
		position: relative;
	}

	// .section-1__title

	&__title {
		font-size: 32px;
		font-weight: 500;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(44 / 32 * 100%);
		position: relative;
		z-index: 4;
		margin-top: 62px;

		@media (max-width: ($md2 + px)) {
			font-size: 30px;
			margin-top: 45px;
		}

		@media (max-width: ($md3 + px)) {
			font-size: 27px;
			max-width: 460px;
			margin-top: 30px;
			br {
				display: none;
			}
		}

		@media (max-width: ($md4 + px)) {
			font-size: 25px;
			margin-top: 20px;
		}
	}

	// .section-1__hand

	&__hand {
		position: relative;
		z-index: 4;
		width: 42px;
		height: 42px;
		box-shadow: 0 10px 30px rgba(163, 169, 183, 0.15);
		background-color: #ffffff;
		border-radius: 50%;
		display: inline-flex;

		margin-left: 11px;
		margin-top: 3px;
		@media (max-width: ($md4 + px)) {
			width: 35px;
			height: 35px;
		}
	}

	&__link {
		color: #fff;

		text-decoration: solid underline rgba($color: #ffffff, $alpha: 0.2) 1px;
		text-underline-offset: 9px;

		&:hover {
			color: #61cb41;
			text-decoration-color: rgba($color: #61cb41, $alpha: 0.2);
		}
	}

	// .section-1__subtitle

	&__subtitle {
		position: relative;
		z-index: 4;
		font-size: 26px;
		font-weight: 400;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(38 / 26 * 100%);
		margin-top: 12px;

		@media (max-width: ($md2 + px)) {
			font-size: 24px;
		}

		@media (max-width: ($md3 + px)) {
			font-size: 20px;
			max-width: 438px;
			br {
				display: none;
			}
		}
		@media (max-width: ($md5 + px)) {
			font-size: 18px;
		}
	}

	// .section-1__list

	&__list {
		max-width: fit-content;
		position: relative;
		z-index: 4;
		padding-left: 48px;
		margin-top: 35px;
		display: flex;
		flex-direction: column;
		gap: 30px;
		@media (max-width: ($md2 + px)) {
			gap: 20px;
			margin-top: 30px;
		}
		@media (max-width: ($md3 + px)) {
			padding-left: 20px;
			margin-top: 20px;
		}

		@media (max-width: ($md5 + px)) {
			gap: 15px;
			padding-left: 10px;
		}
	}
	// .section-1__list-item

	&__list-item {
		display: flex;
		gap: 20px;
		align-items: flex-start;

		@media (max-width: ($md3 + px)) {
			gap: 15px;

			svg {
				max-width: 35px;
				margin-top: -7px;
			}
		}
	}

	// .section-1__list-text

	&__list-text {
		font-size: 20px;
		font-weight: 400;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(30 / 20 * 100%);
		margin-top: -3px;

		@media (max-width: ($md2 + px)) {
			font-size: 18px;
		}
		@media (max-width: ($md3 + px)) {
			font-size: 16px;
		}

		@media (max-width: ($md5 + px)) {
			br {
				display: none;
			}
		}
	}

	// .section-1__button

	&__button {
		width: 525px;

		height: 106px;
		gap: 14px;
		margin-top: 55px;
		position: relative;
		z-index: 3;
		@media (max-width: ($md2 + px)) {
			margin-top: 35px;
		}

		@media (max-width: ($md4 + px)) {
			width: 100%;
		}
		@media (max-width: ($md5 + px)) {
			height: 85px;
			line-height: 1.3;
			margin-top: 20px;
			svg {
				display: none;
			}
		}
	}

	// .section-1__video-block

	&__video-block {
		position: absolute;
		top: 228px;
		right: 0;

		@media (max-width: ($md1 + px)) {
			top: 500px;
			right: 200px;
		}
		@media (max-width: ($md2 + px)) {
			display: none !important;
		}
	}
	&__video-mobile {
		display: none !important;
		@media (max-width: ($md2 + px)) {
			display: flex !important;
			position: static;

			padding-bottom: 70px;
		}
		@media (max-width: ($md3 + px)) {
			padding-bottom: 30px;
		}

		@media (max-width: ($md5 + px)) {
			padding-bottom: 15px;
		}
	}
	// .section-1__man

	&__man {
		position: absolute;
		right: 32px;
		bottom: 0;
		z-index: 3;

		@media (max-width: ($md1 + px)) {
			right: -180px;
			max-width: 750px;
		}
		@media (max-width: ($md2 + px)) {
			right: -280px;
			max-width: 645px;
		}
		@media (max-width: ($md3 + px)) {
			right: -30%;
			max-width: 80%;
		}

		@media (max-width: ($md5 + px)) {
			position: static;
			margin: auto;
		}
	}

	// .section-1__about
	&__start {
		padding-bottom: 110px;
		text-align: center;
		@media (max-width: ($md2 + px)) {
			padding-bottom: 0;
		}
	}

	&__footer {
		max-width: fit-content;
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 25px;
		position: relative;
		z-index: 5;
		@media (max-width: ($md2 + px)) {
			gap: 17px;
		}

		@media (max-width: ($md4 + px)) {
			max-width: none;
		}

		@media (max-width: ($md5 + px)) {
			gap: 10px;
		}
	}
}

.video-block {
	display: flex;
	cursor: pointer;
	gap: 15px;
	align-items: center;

	&:hover {
		color: #61cb41;

		.video-block__play {
			box-shadow: 0 15px 40px rgba(92, 194, 62, 0.2), inset 0 0 20px rgba(255, 255, 255, 0.2);
			background-color: #46d61a;
		}
	}
	&:active {
		.video-block__play {
			box-shadow: none;
			background-color: #61cb41;
		}
	}
	// .video-block__play

	&__play {
		width: 70px;
		height: 70px;
		svg {
			margin-left: 4px;
		}
		@media (max-width: ($md5 + px)) {
			width: 50px;
			height: 50px;
		}
	}

	// .video-block__text

	&__text {
	}
}

.section-2 {
	// .section-2__container
	position: relative;

	&__container {
	}

	// .section-2__items

	&__items {
		margin-top: -54px;
		margin-left: 30px;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 43px;
		position: relative;
		z-index: 4;
		align-items: start;
		@media (max-width: ($md1 + px)) {
			gap: 20px;
			margin-left: 0;
		}

		@media (max-width: ($md2 + px)) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: ($md3 + px)) {
			grid-template-columns: auto;
			gap: 15px;
			max-width: 500px;
			margin: auto;
			margin-top: 30px;
		}
	}

	// .section-2__item

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 30px;
		margin-top: 13px;
		&:nth-child(even) {
			margin-top: 0;
			flex-direction: column-reverse;
			.section-2__quote::after {
				bottom: auto;
				top: -9px;
				transform: scale(-1, 1);
				@media (max-width: ($md2 + px)) {
					top: 30px;
					bottom: unset;
					right: auto;
					left: -20px;
					transform: rotate(-90deg);
				}
			}
			@media (max-width: ($md2 + px)) {
				flex-direction: row-reverse;
			}
		}
		@media (max-width: ($md2 + px)) {
			flex-direction: row-reverse;
			justify-content: start;
			margin-top: 0;
			gap: 25px;
			align-items: start;
		}

		@media (max-width: ($md3 + px)) {
			br {
				display: none;
			}
		}
	}

	// .section-2__quote

	&__quote {
		box-shadow: 0 0 60px rgba(163, 169, 183, 0.15);
		background-color: #ffffff;

		border-radius: 12px;
		padding: 25px 30px;
		position: relative;
		&::after {
			position: absolute;
			content: "";
			width: 0;
			height: 0;
			border-left: 15px solid transparent;
			border-right: 15px solid transparent;
			border-bottom: 15px solid #fff;
			transform: rotate(180deg);
			left: 0;
			right: 0;
			bottom: -14px;
			margin: auto;
			@media (max-width: ($md2 + px)) {
				top: 30px;
				bottom: unset;
				right: auto;
				left: -20px;
				transform: rotate(-90deg);
			}
		}
		@media (max-width: ($md1 + px)) {
			padding: 20px 14px;
		}
		@media (max-width: ($md2 + px)) {
			width: 100%;
		}
	}

	// .section-2__text

	&__text {
		font-size: 16px;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(26 / 16 * 100%);
		text-align: center;
	}

	// .section-2__photo

	&__photo {
		@media (max-width: ($md2 + px)) {
			flex-shrink: 0;
		}
		@media (max-width: ($md5 + px)) {
			flex-shrink: 1;
		}
	}

	// .section-2__line

	&__line {
		position: absolute;
		top: -211px;
		left: -421px;
		z-index: -1;
		margin: auto;
		@media (max-width: ($md2 + px)) {
			left: auto;
			top: 13px;
		}
		@media (max-width: ($md2 + px)) {
			display: none;
		}
		img {
			max-width: unset;
			@media (max-width: ($md1 + px)) {
				max-width: 1860px;
			}
			@media (max-width: ($md2 + px)) {
				max-width: 100%;
			}
		}
	}
}

.section-3 {
	margin-top: 117px;
	@media (max-width: ($md3 + px)) {
		margin-top: 60px;
	}
	// .section-3__container

	&__container {
	}

	// .section-3__title

	&__title {
	}

	// .section-3__ellipse

