	}

	// .section-3__picture

	&__picture {
		margin-top: 35px;
		display: block;
	}
}
.fancybox__image {
	background-color: #fff;
}
.section-4 {
	margin-top: 80px;
	@media (max-width: ($md3 + px)) {
		margin-top: 50px;
	}
	// .section-4__container

	&__container {
	}

	// .section-4__greenblock

	&__greenblock {
		padding: 52px 20px;
		max-width: 1100px;
		margin: auto;
		background-image: url(../img/sec-greenblock1.png);
		@media (max-width: ($md3 + px)) {
			padding: 35px 20px;
		}
		@media (max-width: ($md5 + px)) {
			padding: 25px 15px;
		}
	}

	// .section-4__title

	&__title {
	}

	// .section-4__scroll

	&__scroll {
		display: flex;
		margin-top: 40px;
		gap: 19px;
		align-items: flex-start;
		justify-content: center;

		@media (max-width: ($md3 + px)) {
			margin-top: 25px;
		}
		@media (max-width: ($md5 + px)) {
			gap: 15px;
