}

.section-5 {
	margin-top: 124px;
	// .section-5__container
	@media (max-width: ($md3 + px)) {
		margin-top: 60px;
	}
	@media (max-width: ($md4 + px)) {
		.visible-hand {
			display: flex;
		}
	}
	&__container {
	}

	// .section-5__title

	&__title {
		br {
			@media (max-width: ($md3 + px)) {
				display: none;
			}
		}
	}

	// .section-5__ellipse

	&__ellipse {
		left: -25px;
		top: -25px;

		@media (max-width: ($md2 + px)) {
		}
		@media (max-width: ($md3 + px)) {
			max-width: 320px;
			top: -18px;
			left: -15px;
		}
		@media (max-width: ($md4 + px)) {
			max-width: 280px;
		}

		@media (max-width: ($md5 + px)) {
			max-width: 230px;
			top: -17px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 200px;
			top: -15px;
		}
	}

	// .section-5__items

	&__items {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		gap: 39px 60px;
		margin-top: 43px;
		@media (max-width: ($md1 + px)) {
			gap: 40px 30px;
		}
		@media (max-width: ($md2 + px)) {
			gap: 20px 20px;
		}
		@media (max-width: ($md3 + px)) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media (max-width: ($md4 + px)) {
			grid-template-columns: repeat(6, 300px);
			gap: 10px;
			overflow: scroll;
			margin-top: 0;
		}
	}

	// .section-5__item

	&__item {
