	@media (max-width: ($md3 + px)) {
		margin-top: 60px;
	}
	// .section-17__container

	&__container {
	}

	// .section-17__title

	&__title {
		@media (max-width: ($md2 + px)) {
			br {
				display: none;
			}
		}
	}

	// .section-17__ellipse

	&__ellipse {
		left: -20px;
		bottom: -10px;
		@media (max-width: ($md2 + px)) {
			left: -10px;
		}
		@media (max-width: ($md3 + px)) {
			max-width: 300px;
		}
		@media (max-width: ($md4 + px)) {
			max-width: 270px;
		}
		@media (max-width: ($md5 + px)) {
			max-width: 230px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 190px;
			bottom: -7px;
			left: 0;
		}
	}

	// .section-17__items

	&__items {
		margin-top: 42px;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 30px;
		@media (max-width: ($md1 + px)) {
			gap: 15px;
		}
		@media (max-width: ($md2 + px)) {
			grid-template-columns: repeat(1, auto);
			justify-content: center;
		}
		@media (max-width: ($md3 + px)) {
			margin-top: 30px;
		}
	}

	// .section-17__item

	&__item {
		position: relative;
		&:nth-child(2) {
			.section-17__text {
				margin-top: 15px;
				@media (max-width: ($md1 + px)) {
					margin-top: 10px;
				}
			}
		}
		@media (max-width: ($md5 + px)) {
			height: 250px;
		}
	}

	// .section-17__back

	&__back {
		@media (max-width: ($md5 + px)) {
			height: 100%;
			border-radius: 30px;
		}
		@media (max-width: ($md6 + px)) {
			object-position: 65%;
			filter: brightness(0.7);
		}
	}

	// .section-17__inner

	&__inner {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		color: #fff;
		padding-top: 43px;
		padding-left: 50px;
		padding-right: 30px;
		@media (max-width: ($md1 + px)) {
			padding-left: 30px;
			padding-top: 25px;
		}
		@media (max-width: ($md2 + px)) {
			padding: 40px;
		}
		@media (max-width: ($md4 + px)) {
			padding: 30px;
		}
		@media (max-width: ($md5 + px)) {
			padding: 20px;
		}
	}

	// .section-17__main

	&__main {
	}

	// .section-17__text

	&__text {
		margin-top: 9px;
		@media (max-width: ($md4 + px)) {
			font-size: 16px;
		}
	}

	// .section-17__list

	&__list {
		margin-top: 13px;
		display: flex;
		flex-direction: column;
		gap: 5px;
		@media (max-width: ($md1 + px)) {
			gap: 0;
			margin-top: 8px;
			padding-left: 30px;
		}
	}

	// .section-17__list-item

	&__list-item {
		@media (max-width: ($md4 + px)) {
			font-size: 16px;
		}
	}
}

.section-18 {
