		z-index: 2;
	}

	// .section-20__title

	&__title {
		color: #fff;
	}

	// .section-20__ellipse

	&__ellipse {
		left: 20px;
		bottom: -12px;
		@media (max-width: ($md2 + px)) {
			max-width: 310px;
		}
		@media (max-width: ($md3 + px)) {
			left: 5px;
		}
		@media (max-width: ($md4 + px)) {
			max-width: 260px;
		}
		@media (max-width: ($md5 + px)) {
			max-width: 250px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 220px;
		}
	}

	// .section-20__subtitle

	&__subtitle {
		color: #fff;
	}

	// .section-20__inner

	&__inner {
		color: #fff;
		margin-left: auto;
		padding-right: 74px;
		margin-top: 50px;
		max-width: fit-content;
		@media (max-width: ($md2 + px)) {
			padding-right: 0;
		}
		@media (max-width: ($md3 + px)) {
			max-width: 340px;
			br {
				display: none;
			}
			margin-top: 20px;
		}
		@media (max-width: ($md4 + px)) {
			max-width: 100%;
		}
	}

	// .section-20__main-text

	&__main-text {
		@media (max-width: ($md3 + px)) {
			font-size: 21px;
		}
		@media (max-width: ($md4 + px)) {
			font-size: 19px;
		}
	}

	// .section-20__text

	&__text {
		font-size: 20px;
		font-weight: 500;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(30 / 20 * 100%);
		margin-top: 15px;
		@media (max-width: ($md3 + px)) {
			font-size: 18px;
			margin-top: 8px;
		}
	}

	// .section-20__list

	&__list {
		margin-top: 14px;
		display: flex;
		flex-direction: column;
		gap: 5px;
		@media (max-width: ($md3 + px)) {
			padding-left: 30px;
		}
	}

	// .section-20__list-item

	&__list-item {
		@media (max-width: ($md3 + px)) {
			font-size: 16px;
			&::before {
				top: 10px !important;
			}
		}
	}

	// .section-20__block

	&__block {
		margin-top: 50px;
		box-shadow: 0 0 60px rgba(163, 169, 183, 0.15);
		border-radius: 34px;
		background-color: #ffffff;
		color: #000;

