		@media (max-width: ($md3 + px)) {
			margin-top: 20px;
		}
		@media (max-width: ($md4 + px)) {
			padding: 20px;
		}
	}

	// .section-20__block-icon

	&__block-icon {
		position: absolute;
		right: 29px;
		top: 29px;
		@media (max-width: ($md4 + px)) {
			right: 20px;
			top: 20px;
		}
	}

	// .section-20__block-title

	&__block-title {
		font-family: Buyan;
		font-size: 56px;
		font-weight: 400;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(66 / 56 * 100%);
		@media (max-width: ($md3 + px)) {
			font-size: 50px;
		}
		@media (max-width: ($md4 + px)) {
			font-size: 43px;
