.invisible {
	display: none;
}

._section-title {
	font-family: "Buyan";
	font-size: 66px;
	font-weight: 400;
	font-style: normal;
	letter-spacing: normal;
	line-height: calc(76 / 66 * 100%);
	text-align: center;
	position: relative;
	z-index: 1;
	@media (max-width: ($md1 + px)) {
		font-size: 60px;
	}

	@media (max-width: ($md3 + px)) {
		br {
			display: none;
		}
		font-size: 50px;
	}

	@media (max-width: ($md4 + px)) {
		font-size: 40px;
	}
	@media (max-width: ($md5 + px)) {
		font-size: 36px;
	}
	@media (max-width: ($md6 + px)) {
		font-size: 30px;
	}
	nobr {
		position: relative;
		display: inline-block;
	}
}

._greenblock {
	background-size: cover;
	background-position: top center;
	background-repeat: no-repeat;
	box-shadow: inset 0 0 25px rgba(255, 255, 255, 0.3);
	border-radius: 34px;
	background-color: #43a624;
}

._greenblock-title {
	font-family: "Buyan";
	font-size: 58px;
	font-weight: 400;
	font-style: normal;
	letter-spacing: normal;
	line-height: calc(68 / 58 * 100%);
	text-align: center;
	position: relative;
	color: #ffffff;
	text-align: center;
	@media (max-width: ($md2 + px)) {
		font-size: 45px;
	}

	@media (max-width: ($md3 + px)) {
		font-size: 40px;
		br {
			display: none;
		}
	}
	@media (max-width: ($md4 + px)) {
		font-size: 35px;
	}

	@media (max-width: ($md5 + px)) {
		font-size: 28px;
	}
	@media (max-width: ($md6 + px)) {
		font-size: 25px;
	}
}

._ellipse {
	position: absolute;
	z-index: -1;
	max-width: none;
	@media (max-width: ($md2 + px)) {
		max-width: 350px;
	}
	@media (max-width: ($md3 + px)) {
		max-width: 280px;
	}
	@media (max-width: ($md4 + px)) {
		max-width: 260px;
	}
	@media (max-width: ($md5 + px)) {
		max-width: 200px;
	}
	@media (max-width: ($md6 + px)) {
		max-width: 170px;
	}
}
._section-subtitle {
	font-size: 30px;
	font-weight: 400;
	font-style: normal;
	letter-spacing: normal;
	line-height: calc(40 / 30 * 100%);
	color: #70808c;
	text-align: center;
	margin-top: 10px;
	@media (max-width: ($md3 + px)) {
		font-size: 25px;
	}

	@media (max-width: ($md4 + px)) {
		font-size: 22px;
	}
	@media (max-width: ($md5 + px)) {
		font-size: 20px;
	}
}

._main-title {
	font-size: 42px;
	font-weight: 500;
	line-height: calc(52 / 42 * 100%);
	text-align: center;

	@media (max-width: ($md2 + px)) {
		font-size: 36px;
	}
	@media (max-width: ($md3 + px)) {
		br {
			display: none;
		}
		font-size: 32px;
	}
	@media (max-width: ($md4 + px)) {
		font-size: 30px;
	}
	@media (max-width: ($md5 + px)) {
		font-size: 25px;
	}
	@media (max-width: ($md6 + px)) {
		font-size: 21px;
	}
}

._main-text {
	font-size: 26px;
	font-weight: 500;
	font-style: normal;
	letter-spacing: normal;
	line-height: calc(36 / 26 * 100%);

	@media (max-width: ($md2 + px)) {
		font-size: 24px;
	}
	@media (max-width: ($md3 + px)) {
		font-size: 22px;
	}
	@media (max-width: ($md4 + px)) {
		font-size: 20px;
	}
	@media (max-width: ($md5 + px)) {
		font-size: 18px;
	}
}
._text {
	font-size: 18px;
	font-weight: 400;
	font-style: normal;
	letter-spacing: normal;
	line-height: calc(28 / 18 * 100%);
}

._main-btn {
	box-shadow: 0 15px 40px rgba(255, 204, 0, 0.2), inset 0 0 30px rgba(255, 255, 255, 0.3);
	border-radius: 12px;
	background-color: #ffd400;
	text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
	color: #0d0d0d;
	font-size: 20px;
	font-weight: 500;
	line-height: calc(30 / 20 * 100%);
	color: #0d0d0d;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 15px;

	&:hover {
		background-color: #ffdf3d;
	}

	&:active {
		background-color: #ffd400;
		box-shadow: none;
	}

	@media (max-width: ($md5 + px)) {
		width: 100% !important;
	}
	@media (max-width: ($md6 + px)) {
		padding: 0 10px;
		font-size: 16px;
		br {
			display: none;
		}
	}
}

._form-control {
	width: 395px;
	height: 106px;
	border-radius: 12px;
	border: 1px solid #f1f1f1;
	background-color: #ffffff;
	font-size: 20px;
	font-weight: 400;
	font-style: normal;
	letter-spacing: normal;
	line-height: calc(30 / 20 * 100%);
	padding-left: 46px;
	@media (max-width: ($md3 + px)) {
	}
	@media (max-width: ($md5 + px)) {
		width: 100% !important;
		height: 90px;
	}

	@media (max-width: ($md6 + px)) {
	}
}

._form-control::placeholder {
	color: rgba(13, 13, 13, 0.4);
}
.capture-form__button {
	height: 106px;
	margin-top: 15px;
	@media (max-width: ($md5 + px)) {
		height: 90px;
	}
}
.capture-form__title {
	color: #0d0d0d;
	font-size: 36px;
	font-weight: 500;
	line-height: calc(46 / 36 * 100%);
	margin-bottom: 24px;

	@media (max-width: ($md2 + px)) {
		font-size: 34px;
	}

	@media (max-width: ($md3 + px)) {
		font-size: 30px;
	}
	@media (max-width: ($md5 + px)) {
		font-size: 26px;
	}
	@media (max-width: ($md6 + px)) {
		font-size: 24px;
	}
	b {
		font-weight: 700;
		color: #111111;
	}
}
.policy__label {
	margin-top: 23px;
	@media (max-width: ($md3 + px)) {
		margin-top: 10px;
	}
}
.capture-form {
	display: flex;
	flex-direction: column;
}
.capture-form__label {
	opacity: 0.5;
	color: #0d0d0d;
	font-size: 14px;
	font-weight: 700;
	line-height: calc(22 / 14 * 100%);
	text-transform: uppercase;
	letter-spacing: 0.56px;
	margin-bottom: 10px;
}

.capture-form__phone {
	margin-bottom: 24px;
	@media (max-width: ($md3 + px)) {
		margin-bottom: 15px;
	}
}
.policy {
	font-size: 13px;
	font-weight: 400;
	color: #404040;
	@media (max-width: ($md5 + px)) {
	}
}

.hidden-br {
	display: none;
}

.policy__link {
	color: #61cb41;
	text-decoration: underline;
	text-underline-offset: 5px;
	&:hover {
		color: #3e9922;
		text-decoration-color: #3e9922;
	}
}

.policy__checkbox {
	position: absolute;
	z-index: -1;
	opacity: 0;
	display: block;
	width: 0;
	height: 0;
}

.policy__label {
	position: relative;
	cursor: pointer;
	padding-left: 27px;
	transition: 0.3s all;
	&::before {
		content: "";
		width: 16px;
		height: 16px;
		border-radius: 3px;
		background-color: #ffff;
		border: 1px solid #0d0d0d;
		opacity: 0.1;
		box-sizing: border-box;
		position: absolute;
		left: -23px;
		top: -1px;
	}

	&::after {
		width: 10px;
		height: 7px;
		box-sizing: border-box;
		background-image: url(../img/check.png);
		background-size: contain;
		content: "";
		position: absolute;
		left: -20px;
		top: 4px;
		opacity: 0;
		transition: all 0.1s linear;
		background-repeat: no-repeat;
		background-position: center;
	}
	&:hover {
		color: #3e9922 !important;
		.policy__link {
			color: #3e9922;
			text-decoration-color: #3e9922;
		}
	}
}

.policy__checkbox:checked + .policy__label::after {
	opacity: 1;
	transition: all 0.1s linear;
}

._green-circle {
	box-shadow: 0 15px 40px rgba(92, 194, 62, 0.2), inset 0 0 20px rgba(255, 255, 255, 0.2);
	background-color: #61cb41;
	border-radius: 50%;
}

._check-list {
	padding-left: 33px;

	li {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			width: 23px;
			height: 23px;
			background-size: cover;
			background-repeat: no-repeat;
			background-image: url(../img/checklist.svg);
			top: 3px;
			left: -33px;
		}
	}
}
._green-list {
	padding-left: 47px;
	li {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			width: 6px;
			height: 6px;
			background-color: #61cb41;
			top: 11px;
			left: -17px;
			border-radius: 50%;
		}
	}
}

._icon-center {
	display: flex;
	align-items: center;
	justify-content: center;
}
