.modal-content {
	border: none;
	border-radius: 34px;
	position: relative;
}

.modal-dialog {
	@media (max-width: ($md3 + px)) {
		margin-left: auto;
		margin-right: auto;
	}
	.policy {
		margin-left: 42px;
		@media (max-width: ($md2 + px)) {
			margin-left: 25px;
		}
	}
	.policy__label {
		padding-left: 0;
	}
}

.modal-content .close {
	position: absolute;
	right: -30px;
	top: 0;
	z-index: 10;
	opacity: 1;
	@media (max-width: ($md1 + px)) {
		right: 0;
		top: -30px;
	}
	@media (max-width: ($md3 + px)) {
		right: 15px;
		top: 15px;
		svg path {
			fill: grey;
		}
	}
}

.modal-body {
	border-radius: 34px;
	padding: 0;
}

.callback-modal {
	// .callback-modal__dialog

	&__dialog {
		max-width: 470px;
		@media (max-width: ($md3 + px)) {
			max-width: 400px;
		}
		@media (max-width: ($md5 + px)) {
			max-width: 350px;
		}
	}
	.capture-form__block {
		background-image: url(../img/modal-back.png);
		background-size: cover;
		border-radius: 34px;
	}
	// .callback-modal__content

	&__content {
	}

	.capture-form__title {
		margin-bottom: 15px;
		@media (max-width: ($md3 + px)) {
			margin-bottom: 10px;
		}
	}
	.capture-form__phone {
		margin-bottom: 0;
	}
	.capture-form__subtitle {
		color: #000000;
		font-size: 22px;
		font-weight: 400;
		font-style: normal;
		letter-spacing: normal;
		line-height: calc(32 / 22 * 100%);

		margin-top: 10px;
		@media (max-width: ($md3 + px)) {
			font-size: 20px;
		}
		@media (max-width: ($md5 + px)) {
			font-size: 18px;
		}
	}

	.capture-form__block {
		padding: 35px;

		@media (max-width: ($md3 + px)) {
			padding: 30px;
		}

		@media (max-width: ($md5 + px)) {
			padding: 20px;
		}
	}

	.capture-form__inner {
		flex-direction: column;
	}

	.capture-form__button,
	.capture-form__phone {
		width: 100%;
	}

	.policy {
		margin-left: 35px;
	}
	.policy__label {
		padding-left: 0;
	}
	.capture-form__form {
		margin-top: 20px;
	}
	// .callback-modal__body

	&__body {
	}

	// .callback-modal__container

	&__container {
	}
}

.policy-modal {
	// .policy-modal__dialog

	&__dialog {
		max-width: 700px;
	}

	// .policy-modal__content

	&__content {
	}

	// .policy-modal__body

	&__body {
		padding: 30px;
		padding-top: 40px !important;

		@media (max-width: ($md2 + px)) {
			padding: 20px;
		}

		@media (max-width: ($md5 + px)) {
			padding: 15px;
		}
	}

	// .policy-modal__title

	&__title {
		font-size: 25px;
		text-align: center;
	}

	// .policy-modal__text

	&__text {
		margin-top: 20px;
		font-size: 16px;
		line-height: 1.4;
	}
}

.main-modal {
	// .main-modal__dialog

	.waiting-ellipse {
		@media (max-width: ($md1 + px)) {
			max-width: 350px;
		}
		@media (max-width: ($md2 + px)) {
			max-width: 280px;
			bottom: -10px;
			left: 0;
		}
	}
	&__dialog {
		max-width: fit-content;
		@media (max-width: ($md3 + px)) {
			max-width: 420px;
		}
		@media (max-width: ($md5 + px)) {
			max-width: 380px;
		}
	}

	// .main-modal__content

	&__content {
		background-image: url(../img/modal-back2.png);
		background-size: cover;
	}

	// .main-modal__body

	&__body {
		display: flex;
		padding: 70px 80px 83px 80px;
		gap: 70px;
		@media (max-width: ($md1 + px)) {
			gap: 40px;
			padding: 50px 40px;
		}
		@media (max-width: ($md2 + px)) {
			gap: 30px;
			padding: 30px 20px;
		}
		@media (max-width: ($md3 + px)) {
			padding: 30px;
		}
		@media (max-width: ($md5 + px)) {
			padding: 20px;
			padding-top: 30px;
		}
		@media (max-width: ($md6 + px)) {
			padding: 20px 15px;
			padding-top: 30px;
		}
	}

	// .main-modal__leftside

	&__leftside {
		@media (max-width: ($md3 + px)) {
			display: none;
		}
	}

	// .main-modal__title

	&__title {
		text-align: left;
		@media (max-width: ($md2 + px)) {
			font-size: 45px;
		}
		@media (max-width: ($md3 + px)) {
			font-size: 40px;
		}
	}
	.loboda-ellipse {
		@media (max-width: ($md3 + px)) {
			max-width: 230px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 200px;
		}
	}
	.psychologist-ellipse {
		max-width: 330px;
		left: 0;
		bottom: -10px;
		@media (max-width: ($md1 + px)) {
			max-width: 300px;
		}
		@media (max-width: ($md2 + px)) {
			max-width: 230px;
		}
		@media (max-width: ($md3 + px)) {
			max-width: 200px;
		}
	}
	.group-ellipse {
		@media (max-width: ($md1 + px)) {
			max-width: 350px;
		}
		@media (max-width: ($md2 + px)) {
			max-width: 280px;
			bottom: -10px;
		}
		@media (max-width: ($md3 + px)) {
			max-width: 230px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 230px;
		}
	}

	.single-ellipse {
		@media (max-width: ($md1 + px)) {
			max-width: 350px;
		}
		@media (max-width: ($md2 + px)) {
			max-width: 280px;
			bottom: -10px;
		}
		@media (max-width: ($md3 + px)) {
			max-width: 240px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 220px;
		}
	}
	&__title-mob {
		display: none;
		@media (max-width: ($md3 + px)) {
			display: block;
		}
		@media (max-width: ($md6 + px)) {
			font-size: 38px;
		}
	}

	// .main-modal__subtitle

	&__subtitle {
		text-align: left;
		@media (max-width: ($md2 + px)) {
			font-size: 25px;
		}
	}

	// .main-modal__loboda

	&__loboda {
		display: flex;
		align-items: center;
		gap: 24px;
		margin-top: 25px;
	}

	// .main-modal__loboda-photo

	&__loboda-photo {
		@media (max-width: ($md2 + px)) {
			max-width: 140px;
		}
	}

	._form-control,
	.capture-form__button {
		width: 395px;
		@media (max-width: ($md2 + px)) {
			width: 355px;
		}
	}

	// .main-modal__loboda-text

	&__loboda-text {
	}

	// .main-modal__loboda-name

	&__loboda-name {
	}

	// .main-modal__loboda-proffession

	&__loboda-proffession {
		margin-top: 5px;
		color: #70808c;
	}

	// .main-modal__rightside

	&__rightside {
	}

	// .main-modal__capture-form

	&__capture-form {
	}

	&__ellipse {
		left: 15px;
		bottom: -20px;
		@media (max-width: ($md2 + px)) {
			max-width: 300px;
			left: 10px;
		}
		@media (max-width: ($md3 + px)) {
			left: 0;
			bottom: -10px;
			max-width: 280px;
		}
		@media (max-width: ($md6 + px)) {
			max-width: 250px;
		}
	}
	.capture-form {
		// .capture-form__title

		&__title {
			@media (max-width: ($md2 + px)) {
				font-size: 32px;
			}
			@media (max-width: ($md3 + px)) {
				font-size: 30px;
			}
			@media (max-width: ($md5 + px)) {
				font-size: 26px;
				margin-bottom: 10px;
			}
			@media (max-width: ($md6 + px)) {
				font-size: 24px;
			}
		}

		// .capture-form__label

		&__label {
		}

		// .capture-form__phone

		&__phone {
		}

		// .capture-form__email

		&__email {
		}

		// .capture-form__button

		&__button {
			@media (max-width: ($md4 + px)) {
				font-size: 18px;
			}
		}

		// .capture-form__policy

		&__policy {
		}
	}
}

.effect-modal {
	// .effect-modal__dialog
	@media (max-width: 1330px) {
		.modal-content .close {
			top: -30px;
			right: 0;
		}
	}
	@media (max-width: ($md2 + px)) {
		.visible-hand {
			display: flex;
		}
		.modal-content .close {
			right: 15px;
			top: 15px;
			svg path {
				fill: grey;
			}
		}
	}
	&__dialog {
		max-width: fit-content;
		@media (max-width: ($md1 + px)) {
			max-width: 95%;
			width: fit-content;
		}
	}

	// .effect-modal__content

	&__content {
	}

	// .effect-modal__body

	&__body {
		padding: 50px;
		@media (max-width: ($md1 + px)) {
			max-width: 970px;
			box-sizing: content-box;
		}
		@media (max-width: ($md2 + px)) {
			max-width: 970px;
			box-sizing: content-box;
			padding: 40px;
		}
		@media (max-width: ($md3 + px)) {
			padding: 30px;
			padding-top: 35px !important;
		}
		@media (max-width: ($md4 + px)) {
			padding: 20px;
		}
		@media (max-width: ($md5 + px)) {
			padding: 15px;
		}
		@media (max-width: ($md6 + px)) {
			padding: 10px;
		}
	}
	.section-7 {
		// .section-7__title

		&__title {
		}

		// .section-7__ellipse

		&__ellipse {
		}

		// .section-7__subtitle

		&__subtitle {
		}

		// .section-7__items

		&__items {
			gap: 30px;
			margin-top: 30px;
			@media (max-width: ($md1 + px)) {
				gap: 15px;
			}
			@media (max-width: ($md2 + px)) {
				margin-top: 0;
			}
		}

		// .section-7__item

		&__item {
		}

		// .section-7__item-inner

		&__item-inner {
		}

		// .section-7__picture

		&__picture {
		}

		// .section-7__text

		&__text {
		}

		// .section-7__main-text

		&__main-text {
		}

		// .section-7__small-text

		&__small-text {
		}
	}
}
