@font-face {
	font-family: "Onest";
	src: url("../fonts/Onest-Bold.eot");
	src: url("../fonts/Onest-Bold.eot?#iefix") format("embedded-opentype"),
		url("../fonts/Onest-Bold.woff2") format("woff2"), url("../fonts/Onest-Bold.woff") format("woff"),
		url("../fonts/Onest-Bold.ttf") format("truetype"), url("../fonts/Onest-Bold.svg#Onest-Bold") format("svg");
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

